<template>
   <nav class="navbar navbar-expand-lg bg-body-tertiary pointer">
        <div class="container-fluid">
            <a class="navbar-brand" @click="[$router.push('/'),who()]"><b>Who?</b></a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown"
                aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNavDropdown">
                <ul class="navbar-nav">
                    <li class="nav-item nav-item1">
                        <a class="nav-link" :class={active:career} @click="onAlert">Career</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" :class={active:portfolio} @click="[$router.push('/portfolio'),portfolioOnoff()]">Portfolio</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" :class={active:contact} @click="[$router.push('/hobby'),contactOnoff()]">Contact</a>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
export default {

    data() {
        return {
            career : false,
            portfolio : false,
            contact : false,
        }
    },

    methods: {
        onAlert(){
            alert('I am still a new developer. ')
        },
        who(){
            this.career = false;
            this.portfolio = false;
            this.contact = false;
        },
        careerOnoff(){
            this.career = true;
            this.portfolio = false;
            this.contact = false;
        },
        portfolioOnoff(){
            this.career = false;
            this.portfolio = true;
            this.contact = false;
        },
        contactOnoff(){
            this.career = false;
            this.portfolio = false;
            this.contact = true;
        }
    },
}
</script>

